





































import {LoginForm} from "@/types/form/LoginForm";
import {PERMISSION_ACTION, PermissionActions} from "@/types/store/PermissionStore";
import {Component, Vue} from "vue-property-decorator";
import {Action} from "vuex-class";

@Component
export default class LoginView extends Vue {
  @Action(PERMISSION_ACTION.UPDATE_CURRENT_PERMISSION)
  updatePermission!: PermissionActions["UPDATE_CURRENT_PERMISSION"];

  formData: LoginForm = {
    mail: "",
    password: "",
  };

  redirect(name: string): void {
    this.$router.push({name});
  }
  goToNext(name: string): void {
    this.updatePermission(name);
    this.$router.push({name: "App"});
  }
}
