
























import {TABS_ACTION, TabsActions} from "@/types/store/TabStore";
import {Component, Vue} from "vue-property-decorator";
import {Action, Getter} from "vuex-class";

@Component
export default class NavMenu extends Vue {
  @Getter("permission/currentPermission") readonly currentPermission!: string;

  @Action(TABS_ACTION.UPDATE_CURRENT_TAB)
  updateTab!: TabsActions["UPDATE_CURRENT_TAB"];

  goToTab(tab: any): void {
    this.$router.push({query: {tab: tab.name}});
    this.updateTab(tab.name);
    this.currentIndex = tab.index;
  }

  currentIndex: number = 0;

  mounted(): void {
    const tab =
      this.currentPermission === "ROOT"
        ? this.rootTabs[0]
        : this.currentPermission === "ADMIN"
        ? this.adminTabs[0]
        : this.userTabs[2];
    this.goToTab(tab);
  }

  get isRoot(): boolean {
    return this.currentPermission === "ROOT";
  }

  get upperTabs(): any[] {
    return this.isRoot
      ? []
      : this.userTabs.filter((tab) => {
          return tab.permission === this.currentPermission || tab.permission === "SHARED";
        });
  }

  get lowerTabs(): any[] {
    return this.isRoot
      ? this.rootTabs
      : this.adminTabs.filter((tab) => {
          return tab.permission === this.currentPermission || tab.permission === "SHARED";
        });
  }

  get eventTabs(): any[] {
    return this.isRoot ? [] : this.demoTabs;
  }

  // move to vuex store when api done
  userTabs = [
    {index: 0, title: "Sequence Store", name: "SequenceStore", permission: "ADMIN"},
    {index: 1, title: "Sequence Store", name: "UserSequenceStore", permission: "USER"},
    {index: 2, title: "My Labs", name: "Lab", permission: "SHARED"},
    {index: 3, title: "Sequence Manager", name: "SequenceManager", permission: "ADMIN"},
    {index: 4, title: "Sequence Manager", name: "UserSequenceManager", permission: "USER"},
    {index: 5, title: "Project Manager", name: "ProjectManager", permission: "SHARED"},
    // {index: 6, title: "Project Worker", name: "ProjectWorker", permission: "SHARED"},
    // {index: 7, title: "Transducer DB Settings", name: "TransducerDbSettings", permission: "SHARED"},
    {index: 8, title: "Transducer DB", name: "TransducerDbDevice", permission: "SHARED"},
  ];

  adminTabs = [
    {index: 9, title: "User Account Management", name: "UserAccountManagement", permission: "ADMIN"},
    {index: 10, title: "Plan Management", name: "PlanManagement", permission: "ADMIN"},
    {index: 11, title: "Plan Setting", name: "PlanSetting", permission: "ADMIN"},
    {index: 12, title: "Device Management", name: "IoT", permission: "ADMIN"},
    {index: 13, title: "OTA Server", name: "OTA", permission: "ADMIN"},
  ];

  rootTabs = [{index: 14, title: "Admin Account Management", name: "AdminAccountManagement", permission: "ROOT"}];

  demoTabs = [{index: 14, title: "DemoEvent", name: "DemoEvent", permission: "SHARED"}];
}
